<template>
    <div class="m-flower" :loading="loading">
        <h1 class="m-flower-title"></h1>
        <el-divider class="m-flower-desc"></el-divider>

        <div class="m-flower-search">
            <el-row>
                <el-col :span="7">
                    <el-select
                        class="u-server"
                        v-model="server"
                        filterable
                        placeholder=""
                    >
                        <el-option
                            v-for="item in servers"
                            :key="item"
                            :label="item"
                            :value="item"
                        >
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="7">
                    <el-select
                        class="u-type"
                        v-model="type"
                        placeholder=""
                    >
                        <el-option
                            v-for="item in types"
                            :key="item"
                            :label="item"
                            :value="item"
                        >
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="7">
                    <el-select
                        class="u-level"
                        v-model="level"
                        placeholder=""
                    >
                        <el-option
                            v-for="item in levels"
                            :key="item"
                            :label="item"
                            :value="item"
                        >
                            <span>{{ item ? item : "" }}</span>
                            <span v-if="item">  {{ color(item) }}</span>
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="3">
                    <el-button
                        class="u-button"
                        type="primary"
                        icon="el-icon-search"
                        :disabled="isGuest"
                        @click="search"
                        ></el-button
                    >
                </el-col>
            </el-row>
        </div>
        <div class="m-flower-submit">
            <el-alert
                class="u-tip"
                v-if="isGuest"
                title=""
                type="info"
                show-icon
            ></el-alert>
        </div>
        <div class="m-flower-tip el-alert el-alert--info is-light">
            <a
                href="https://v2.jx3box.com/dashboard/#/profile"
                target="_blank"
                ></a
            >
        </div>
        <div class="m-flower-box" v-if="mode">
            <div class="m-flower-result" v-if="data && data.length">
                <el-table
                    :data="data"
                    :default-sort="{ prop: 'date', order: 'descending' }"
                >
                    <el-table-column prop="server" label=""
                        >{{ server }}
                    </el-table-column>
                    <el-table-column prop="map" label="">
                    </el-table-column>
                    <el-table-column prop="unit" label="" sortable>
                    </el-table-column>
                    <el-table-column prop="price" label="" sortable>
                    </el-table-column>
                    <el-table-column
                        prop="time"
                        label=""
                        sortable
                        :formatter="dateFormat"
                    >
                    </el-table-column>
                </el-table>
            </div>
            <el-alert
                v-if="!data.length && done"
                class="m-archive-null"
                title=""
                type="info"
                center
                show-icon
            >
            </el-alert>
            <el-button
                class="m-archive-more"
                :class="{ show: hasNextPage }"
                type="primary"
                :loading="loading"
                @click="appendPage(++page)"
                ></el-button
            >
            <el-pagination
                class="m-archive-pages"
                :page-size="15"
                background
                :hide-on-single-page="true"
                @current-change="changePage"
                :current-page.sync="page"
                layout="total, prev, pager, next, jumper"
                :total="total"
            >
            </el-pagination>
        </div>
        <div class="m-flower-overview" v-else>
            <el-table
                v-if="overview && overview.length"
                :data="overview"
                :default-sort="{ prop: 'name', order: 'ascending' }"
            >
                <el-table-column
                    prop="name"
                    label=""
                    width="200px"
                    :formatter="nameFormat"
                >
                </el-table-column>
                <el-table-column prop="map" label="" min-width="300px"
                    ><template slot-scope="scope">
                        <el-tooltip
                            class="u-line-wrapper"
                            effect="dark"
                            content=""
                            placement="top"
                        >
                            <span
                                class="u-line"
                                v-for="line in scope.row.map"
                                :key="line"
                                v-clipboard:copy="onlyLineNumber(line)"
                                v-clipboard:success="onCopy"
                                v-clipboard:error="onError"
                                >{{ line.replace(" ", "") }}</span
                            >
                        </el-tooltip>
                    </template></el-table-column
                >
                <el-table-column
                    prop="price"
                    label=""
                    width="160px"
                >
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import servers from "@jx3box/jx3box-data/data/server/server_list.json";
import User from "@jx3box/jx3box-common/js/user";
import { getFlowerPrice, getFlowerPrices } from "../service/flower";
import dateFormat from "../utils/moment";
import { setServer, getServer } from "../service/server";
import colormap from "../assets/data/flower_colormap.json";
import colors from "../assets/data/flower_colormap2.json";

export default {
    name: "Flower",
    props: [],
    data: function() {
        return {
            servers,
            server: "",
            types: ["", "", "", "", "", ""],
            type: "",
            level: "",
            colormap,
            overview: "",
            data: [],
            total: 1,
            pages: 1,
            page: 1,
            loading: false,
            done: false,
            mode: 1,
        };
    },
    computed: {
        levels: function() {
            let levels = [""];
            if (this.type) {
                return levels.concat(Object.keys(this.colormap[this.type]));
            }
            return levels;
        },
        isGuest: function() {
            // return !User.isLogin();
            return false;
        },
        hasNextPage: function() {
            return this.total > 1 && this.page < this.pages;
        },
    },
    methods: {
        color: function(level) {
            if (this.type) {
                return this.colormap[this.type][level];
            }
            return "";
        },
        check: function() {
            this.server = this.server || "";
            this.type = this.type || this.types[0];
            return this.level ? 1 : 0;
        },
        search: function() {
            this.mode = this.check();
            // 
            if (this.mode) {
                this.page = 1; //
                this.loadData(1);
                // 
            } else {
                this.loadOverview();
            }
            setServer(this.server);
        },
        dateFormat: function(row, column) {
            return dateFormat(row.time * 1000);
        },
        nameFormat: function(row, column) {
            return row.name + " ( " + colors[row.name] + " ) ";
        },
        loadOverview: function() {
            this.loading = true
            return getFlowerPrices({
                server: this.server,
                flower: this.type,
            }).then((res) => {
                let overview = [];
                for (let name in res.data) {
                    overview.push({
                        name: name,
                        price: res.data[name]["max"] + "",
                        map: res.data[name]["maxLine"].slice(0, 5),
                    });
                }
                this.overview = overview;
            });
        },
        loadData: function(i, append = false) {
            this.loading = true
            return getFlowerPrice({
                server: this.server,
                flower: this.level + this.type,
                pageIndex: i,
            })
                .then((res) => {
                    if (append) {
                        this.data = this.data.concat(res.data.data);
                    } else {
                        this.data = res.data.data;
                    }
                    this.total = res.data.page.total;
                    this.pages = res.data.page.pageTotal;
                })
                .then(() => {
                    this.$message({
                        message: "",
                        type: "success",
                    });
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        appendPage: function(i) {
            this.loading = true;
            this.loadData(i, true);
        },
        changePage: function(i) {
            this.loading = true;
            this.loadData(i).then(() => {
                window.scrollTo(0, 0);
            });
        },
        onCopy: function(val) {
            this.$notify({
                title: "",
                message: " : " + val.text,
                type: "success",
            });
        },
        onError: function() {
            this.$notify.error({
                title: "",
                message: "",
            });
        },
        onlyLineNumber: function(val) {
            return val.replace("", "");
        },
    },
    filters: {},
    mounted: function() {
        getServer().then((server) => {
            if (server) {
                this.server = server;
            }
        });
    },
    components: {},
};
</script>

<style lang="less">
@import "../assets/css/flower.less";
</style>
